body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* { 
  background-color: rgb(90, 90, 90);
}

.app-container {
  display:flex;
  flex-direction: row;
  overflow-x: hidden;
}

h1 {
  font-size: 2em;
  font-weight: lighter;
  text-align: center;
  color: #cccccc;
  font-family: 'Spicy Rice', cursive;
  text-transform: uppercase;
  margin: 10px;
}


.chat {
  width: 100%;
  margin-left: auto; 
  margin-right: auto;
  font-family:tahoma; 
  font-size:12px; 
  padding: 10px;
  color: 	#E8E8E8;
}

.chat .content { 
  padding: 15px 15px 25px 15px;
  border-radius:5px;
  overflow-y: scroll; 
  border: 1px solid #CCC;
  margin-top: 10px; 
  height: 200px; 
}

.chat .content .content-message {
  line-height: 20px;
  margin: 0;
}

.chat .chat-input {
  border-radius:2px; 
  border: 1px solid #ccc;
  margin-top: 10px; 
  padding: 5px; 
  width: 98%;
  color: #E8E8E8;
}

.chat .chat-status {
  display:block;
  float:left;
  margin-top:15px; 
  margin-right: 5px;
}

.grid {
  width: 400px;
  background-color: black;
  height: 100%;
  display: flex;
  align-items: flex-start;
  flex-wrap: wrap;
  padding: 10px;
  position:relative;
}

.grid-tile {
  background-color: rgb(116, 38, 14);
  width: 32px;
  height: 32px;
  border: 1px solid white;
  display: flex;
}

.grid-tile-row {
  display: flex;
  align-items: flex-start;
  flex-wrap: nowrap;
}

.player-character {
  background-color: blue;
  width: 32px;
  height: 32px;
  position: absolute;
}
